import React, { Component } from 'react';

class App extends Component {
  constructor(props){
    super(props);
    this.state = {data : []};
  }
  componentDidMount() {
        const url = "https://api.nasa.gov/planetary/apod?api_key=4uXKIq0Mfp3ODvsgX1Q6h2HlGfbnyymn1ebd30lI";
        fetch(url)
            .then(result => result.json())
            .then(result => {
                this.setState({
                    data: result
                });
            });
    }
  render() {
    let data = this.state.data;
    let flexColumn = {display:'flex',alignItems: 'center',justifyContent:'center',flexDirection:'column'};
    let h1Style = {textAlign:'center'};
    let imgStyle = {maxWidth:'50em'};
    return (
      <div class="container">
        <div>
          <h1 style={h1Style}>Nasa picture of the day</h1>
        </div>
        <div style={flexColumn}>
          <img src={data.url} alt="Picture of the day" style={imgStyle} />
          <br />
          <p>Image credits: Public domain</p>
        </div>
        <div>
          <h2>Image url:</h2>
          <a href={data.url}>Normal</a><br />
          <a href={data.hdurl}>Hd</a>
        </div>
        <div>
          <h2>Image description:</h2>
          <p>{data.explanation}</p>
        </div>
      </div>
    );
  }
}

export default App;
